	module.exports = {"data":{"content":{"content":"<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/HDvrCYc.png\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/HDvrCYc.png title=\"Coders of the Caribbean report for Calpa\" /></a></p>\n<p>寫了一天Code後，我就把那些代碼放置在Codingame上面。今天它寄電郵給我說有比賽結果。對於這一次的結果，我完全不感到意外。因為很多地方都沒有去優化，比如說如何去計算各種行動的優先權重。這裡就不放上代碼了，還是下一次拿到更高的排名才放出來吧 XD</p>\n<p>看來我還是要好好地刷算法才行。。。</p>\n","title":"Coders of the Caribbean Online Contest 後記","createdDate":"2017-04-25T15:35+08:00","headerImgur":null,"id":"6c0KLho13OYqMuoumg26Ye","toc":[],"tags":"Codingame"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"6c0KLho13OYqMuoumg26Ye"}}

